import React, { useContext } from "react";
import { GlobalState } from "../Context/GlobalState";
import Navigation from "./Navigation";

const BurgerMenu = () => {
  const { activated, setActivated } = useContext(GlobalState);

  return (
    <div
      activated={activated}
      onClick={() => {
        setActivated(!activated);
      }}
      className="burger-menu"
    >
      <div
        style={
          activated
            ? { backgroundColor: "rgb(230, 64, 4)" }
            : { backgroundColor: "rgb(184, 52, 3)" }
        }
        className="div1"
      ></div>
      <div
        style={
          activated
            ? { backgroundColor: "rgb(230, 64, 4)" }
            : { backgroundColor: "rgb(184, 52, 3)" }
        }
        className="div1"
      ></div>
      <div
        style={
          activated
            ? { backgroundColor: "rgb(230, 64, 4)" }
            : { backgroundColor: "rgb(184, 52, 3)" }
        }
        className="div1"
      ></div>
      <Navigation activated={activated} />
    </div>
  );
};

export default BurgerMenu;
